.section {
    min-height: 50rem;
    border-bottom-style: solid;
    border-bottom-color: white;
    border-bottom-width: 1px;
}

.container {
    display: flex;
}

.col-f {
    flex-direction: column;
    gap: 1rem;
}

.col-f-0 {
    flex-direction: column;
}

.row-f {
    flex-direction: row;
    gap: 1rem;
}

.row-f-0 {
    flex-direction: row;
}

.flex-1 {
    flex: 1;
}

.f-wrap {
    flex-wrap: wrap;
}

.f-wrap-r {
    flex-wrap: wrap-reverse;
}

.f-around {
    justify-content: space-around;
}

.f-evenly {
    justify-content: space-evenly;
}

.f-between {
    justify-content: space-between;
}

.f-center-c {
    justify-content: center;
    align-items: center;
}

.f-center-l {
    justify-content: center;
    align-items: start;
}

.f-center-r {
    justify-content: center;
    align-items: end;
}

.f-center {
    align-items: center;
}

.f-content {
    margin: 0 auto;
    max-width: 1440px;
    flex: 1;
}

.footer-child {
    min-width: 150px;
}

.grid {
    width: calc(100%);
    display: grid;
    padding-block: 1rem;
}

.img {
    border-radius: 30px;
    min-width: 240px;
    width: calc(100%);
    min-height: 300px;
    height: 480px;
    filter: drop-shadow(5px 5px 3px rgb(182, 210, 255)) saturate(120%);
    object-fit: cover;
}

.img-s {
    border-radius: 10px;
    min-width: 240px;
    width: calc(100%);
    min-height: 300px;
    height: 200px;
    object-fit: cover;
}

.nav {
    padding: 1rem;
    padding-inline: 10rem;
    backdrop-filter: blur(10px);
}

.text {
    line-height: 1.15cm;
}

.t-center {
    text-align: center;
}